import React from 'react'
import { Box, Card, Grid, Hidden } from '@material-ui/core'
import clsx from 'clsx'

import { Login } from '../../containers'
import { useStylesTheme } from '../../config'
import imageBackground from '../../assets/Iniciar sesioÃÅn-1.png';
import logoICV from '../../assets/logo_icv_gris.png'

const LoginPage = () => {
    const classes = useStylesTheme();

    const addCard = () => {
        return (
            <Card className={clsx(classes.pageCard, classes.noNavBarMargin)}>
                <div style={{textAlign: 'center', width: '100%', marginTop: 60}}>
                    <img src={logoICV} height={80} alt="" />
                    <p style={{marginTop: 0, fontSize: 6.5, textDecoration: 'underline'}}> <strong>INGENIER√çA CIVIL VICENTE</strong> </p>
                    <p style={{fontSize: '2em'}}>Iniciar sesi√≥n</p>
                </div>
                <Login />
            </Card>
        )
    }

    return (
            <div style={{height: '100%', display: 'block'}}>
                <Hidden smDown>
                    <div style={{float: 'left', width: '50%', height: '100%'}}>
                        {addCard()}
                    </div>
                </Hidden>
                <Hidden smDown>
                    <div style={{float: 'left', width: '50%', height: '100%', display: 'block'}}>
                        <img src={imageBackground} style={{objectFit: 'cover', width: '100%', height: '100%'}}/>
                    </div>
                </Hidden>
                <Hidden mdUp>
                    <div>
                        {addCard()}
                    </div>
                </Hidden>
                
                {/* <Grid container spacing={0}>
                <Grid className={classes.pageContainer} item xs={12} md={6}>
                    <Card className={clsx(classes.pageCard, classes.noNavBarMargin)}>
                        <div style={{textAlign: 'center', width: '100%', marginTop: 60}}>
                            <img src={logoICV} height={80} alt="" />
                        </div>
                        <Login />
                    </Card>
                </Grid>
                
                    <Grid item xs={12} md={6}>
                    <div style={{display: 'block', width: '100%'}}>
                        <img src={imageBackground} style={{objectFit: 'cover', width: '100%'}}/>
                    </div>
                    </Grid>
                </Hidden>
            </Grid> */}
            </div>
            
    )
}

export default LoginPage
